import CONF from './config'
import Api from '@/utils/api'

const manageAuth = CONF.manageAuth.API

export default {
  /**
   * [保存App信息]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async getPoliceInfo(params = {}) {
    const res = await Api.get(manageAuth['getPoliceInfo'], params)
    return res
  },
  async dealInfo(params = {}) {
    const res = await Api.get(manageAuth['dealInfo'], params)
    return res
  },
  async resetPassword(params = {}) {
    const res = await Api.get(manageAuth['resetPassword'], params)
    return res
  },
  async saveOfficerInfo(params = {}) {
    const res = await Api.get(manageAuth['saveOfficerInfo'], params)
    return res
  },
  async getListMenu(params = {}) {
    const res = await Api.get(manageAuth['getListMenu'], params)
    return res
  }
}
