<template>
  <div class="power-manage">
    <div class="clearfix">
      <el-form :inline="true" :model="formInline" class="form-inline">
        <el-form-item label="姓名">
          <el-input v-model="formInline.officerName" size="small" placeholder="请输入" clearable></el-input>
        </el-form-item>
        <el-form-item label="警号">
          <el-input v-model="formInline.officerNo" size="small" placeholder="请输入" clearable></el-input>
        </el-form-item>
        <el-form-item label="单位">
          <el-input v-model="formInline.officerStation" size="small" placeholder="请输入" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="small" @click="onQuery">查询</el-button>
        </el-form-item>
      </el-form>
      <el-button icon="el-icon-plus" type="primary" size="small" @click="onOpenDialog('新增')" style="float: right;">新增人员
      </el-button>
    </div>
    <div class="table-box">
      <el-table
        :data="tableData"
        border>
        <el-table-column
          prop="officerName"
          label="姓名"
          width="120">
        </el-table-column>
        <el-table-column
          prop="officerNo"
          label="警号">
        </el-table-column>
        <el-table-column
          prop="officerStation"
          label="所属单位">
        </el-table-column>
        <el-table-column
          prop="officerPhone"
          label="联系方式">
        </el-table-column>
        <el-table-column
          prop="status"
          label="当前状态">
          <template slot-scope="scope">
            {{scope.row.status===0?'有效':'无效'}}
          </template>
        </el-table-column>
        <el-table-column
          prop="operate"
          label="操作"
          width="200">
          <template slot-scope="scope">
            <el-button @click="onOpenDialog('编辑',scope.row)" type="text" size="small">编辑</el-button>
            <el-button @click="handlePassword(scope.row)" type="text" size="small">密码重置</el-button>
            <el-button @click="handleStatus(scope.row)" type="text" size="small">
              {{scope.row.status===0?'禁用':'启用'}}
            </el-button>
            <el-button @click="handleDelete(scope.row)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        style="text-align: right; padding: 20px 0;"
        background
        @current-change="currentChange"
        :current-page="currentPage"
        :page-size="10"
        layout="total, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      :title="dialogTitle"
      @close="closeDialog"
      :visible.sync="dialogVisible"
      width="50%">
      <div>
        <el-form label-width="80px" :model="formPower" :rules="rules" ref="ruleForm">
          <el-form-item label="姓名" prop="officerName">
            <el-input v-model="formPower.officerName" placeholder="请输入" clearable></el-input>
          </el-form-item>
          <el-form-item label="警号" prop="officerNo">
            <el-input v-model="formPower.officerNo" placeholder="请输入" clearable></el-input>
          </el-form-item>
          <el-form-item label="联系方式" prop="officerPhone">
            <el-input v-model.number="formPower.officerPhone" placeholder="请输入" clearable></el-input>
          </el-form-item>
          <el-form-item label="所属单位" prop="officerStation">
            <el-input v-model.number="formPower.officerStation" placeholder="请输入" clearable></el-input>
          </el-form-item>
          <el-form-item label="权限选择" prop="menuIds">
            <el-checkbox-group v-model="formPower.menuIds">
              <el-checkbox v-for="(item,index) in powerList" :label="item.value" :key="index"
                           style="margin-left: 20px;">
                {{item.label}}
              </el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog">返回</el-button>
        <el-button size="small" type="primary" @click="onSaveFun">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Service from './service'

export default {
  name: 'ManageAuthority',
  data() {
    return {
      formInline: {
        officerName: '',
        officerNo: '',
        officerStation: ''
      },
      options: [{
        value: '选项1',
        label: '黄金糕'
      }, {
        value: '选项2',
        label: '双皮奶'
      }],
      value: '',
      tableData: [],
      total: 0,
      currentPage: 1,
      dialogVisible: false,
      dialogTitle: '新增人员',
      formPower: {
        officerName: '',
        officerNo: '',
        officerPhone: '',
        officerStation: '',
        menuIds: []
      },
      powerList: [],
      rules: {
        officerName: [
          { required: true, message: '请输入名称', trigger: 'blur' },
        ],
        officerNo: [
          { required: true, message: '请输入警号', trigger: 'blur' }
        ],
        officerPhone: [
          { required: true, message: '请输入电话号', trigger: 'blur' },
        ],
        officerStation: [
          { required: true, message: '请输入单位', trigger: 'blur' },
        ],
        menuIds: [
          { required: true, message: '请选择权限', trigger: 'change' }
        ]
      },
      editId: ''
    }
  },
  methods: {
    // 权限菜单列表
    async getListMenu() {
      const data = await Service.getListMenu()
      this.powerList = data.data.map(item => {
        return {
          label: item.name,
          value: item.id + ''
        }
      })
    },
    // 新增和编辑
    async saveOfficerInfo(params) {
      const data = await Service.saveOfficerInfo(params)
      this.closeDialog()
      this.dialogVisible = false
      this.getPoliceInfo()
    },
    // 重置密码
    async resetPassword(row) {
      const data = await Service.resetPassword({ id: row.id })
      this.$message({
        type: 'success',
        message: '密码重置成功!'
      });
    },
    // 查询主列表
    async getPoliceInfo() {
      let params = {
        currentPage: this.currentPage,
        ...this.formInline
      }
      const data = await Service.getPoliceInfo(params)
      this.tableData = data.data
      this.total = data.totalSize
    },
    // 删除和改状态
    async dealInfo(params, status) {
      await Service.dealInfo(params)
      if (status === 'delete') {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      } else if (status === 'status') {
        this.$message({
          type: 'success',
          message: '状态修改成功!'
        });
      }
      this.getPoliceInfo()
    },
    onQuery() {
      this.currentPage = 1
      this.getPoliceInfo()
    },
    currentChange(v) {
      this.currentPage = v
      this.getPoliceInfo()
    },
    onOpenDialog(name, row) {
      this.dialogTitle = name + '人员'
      if (name === '编辑') {
        this.editId = row.id
        this.formPower.officerName = row.officerName
        this.formPower.officerNo = row.officerNo
        this.formPower.officerPhone = row.officerPhone
        this.formPower.officerStation = row.officerStation
        this.formPower.menuIds = row.menuIds.split(',')
      }
      this.dialogVisible = true
    },
    handleClick() {
      this.$message({
        type: 'success',
        message: '启用成功!'
      });
    },
    handlePassword(row) {
      this.$confirm('此操作将重置密码, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.resetPassword(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    handleStatus(row) {
      let params = {
        id: row.id,
        status: row.status === 0 ? 1 : 0
      }
      this.$confirm('此操作将修改状态, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.dealInfo(params, 'status')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    handleDelete(row) {
      this.$confirm('确认删除, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = {
          id: row.id,
          deleted: 1
        }
        this.dealInfo(params, 'delete')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    onSaveFun() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          let params = {
            ...this.formPower,
            menuIds: this.formPower.menuIds.join(',')
          }
          if (this.editId) {
            params['id'] = this.editId
          }
          this.saveOfficerInfo(params)
        } else {
          return false;
        }
      })
    },
    closeDialog() {
      this.$refs.ruleForm.resetFields()
      this.formPower = {
        officerName: '',
        officerNo: '',
        officerPhone: '',
        officerStation: '',
        menuIds: []
      }
      this.editId = ''
      this.dialogVisible = false
    }
  },
  mounted() {
    this.getListMenu()
    this.getPoliceInfo()
  }
}
</script>

<style lang="less" scoped>
  .clearfix:after {
    content: "";
    display: block;
    clear: both;
  }

  .clearfix {
    zoom: 1;
  }

  .power-manage {
    text-align: left;
    .form-inline {
      float: left;
    }
  }
</style>
